import React from 'react';

const About = function () {
    return(
        <div className="section text-left" id="about">
        <h3 className="title">ABOUT</h3>
        <div className="about-description text-left">
            I am GIS and Remote Sensing Graduate from Dedan Kimathi University of Technology. Currently I am working as a
            freelance Web and Mobile GIS developer since May 2020. I have had the priviledges of integrating web maps on existing website,
            creating fully fledge Web GIS applications and a number of mobile application with React Native. 
            I am inclined towards opensource mapping/visualization libraries such as:
                <ul className="">
                    <li>Leaflet</li>
                    <li>MapboxGL</li>
                    <li>Openlayers</li>
                    <li>D3js</li>
                    <li>ThreeJS</li>
                </ul>
            When working on fully fledge application I am inclined towards NodeJS and Django together with modern JavaScript libraries such as
            React, Angular or Vuejs; or the Vanilla JavaScript with Jquery.
            
            I am passionate about location information and the insights that can be derived from this data through scripting or opensource or commercial
            GIS software. 
        </div>
      </div>
    )
}

export default About;